code {
  font-family: 'Space Mono', source-code-pro, Menlo, Monaco, Consolas,
    'Courier New', monospace;
}

.container {
  max-width: 980px;
  margin: 0 auto;
}

/* removing cursors */
html {
  cursor: auto;
}

a,
button {
  cursor: pointer;
}

/* https://medium.com/@matuzo/writing-css-with-accessibility-in-mind-8514a0007939 */
.nes-pointer {
  cursor: pointer;
}

.nes-btn {
  cursor: pointer;
}

.nes-radio + span {
  cursor: pointer;
}
.nes-checkbox + span {
  cursor: pointer;
}
.nes-select select {
  cursor: pointer;
}

.min-w5 {
  min-width: 16rem;
}

.hover-success:hover {
  color: #209cee;
}
.hover-error:hover {
  color: #ce372b;
}

.nes-list.is-disc li::before {
  color: inherit;
}

.fixed-width {
  width: 100px;
}

.table-design td,
.table-design th {
  padding: 0 0.25rem;
  border-right: 1px solid #e0e0e0;
}

.table-fixed-ml tbody th {
  min-width: 200px;
}

.mw8-ml {
  max-width: 64rem;
}

@media (min-width: 1209px) {
  .tbth-min-w236-ml tbody th {
    min-width: 236px;
  }

  .mw8-ml {
    max-width: 1209px;
  }

  .ttdh-w108-ml td,
  .ttdh-w108-ml th {
    /* min-width: 108px; */
    width: 108px;
  }

  .ml128-ml {
    margin-left: 128px;
  }

  .table-fixed-ml {
    table-layout: fixed;
  }
}

