.fixed-width {
  width: 100px;
}

.table-design td,
.table-design th {
  padding: 0 0.25rem;
  border-right: 1px solid #e0e0e0;
}

.table-fixed-ml tbody th {
  min-width: 200px;
}

.mw8-ml {
  max-width: 64rem;
}

@media (min-width: 1209px) {
  .tbth-min-w236-ml tbody th {
    min-width: 236px;
  }

  .mw8-ml {
    max-width: 1209px;
  }

  .ttdh-w108-ml td,
  .ttdh-w108-ml th {
    /* min-width: 108px; */
    width: 108px;
  }

  .ml128-ml {
    margin-left: 128px;
  }

  .table-fixed-ml {
    table-layout: fixed;
  }
}
